import React, { useState,useReducer } from "react";

// reactstrap components
import {
  Button,
  ButtonGroup,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  Label,
  FormGroup,
  Input,
  Table,
  Row,
  Col,
  UncontrolledTooltip,
} from "reactstrap";
import pic from '../assets/img/multimedia.png'
import { css } from "@emotion/react";
import ClipLoader from "react-spinners/ClipLoader";
import NotificationAlert from "react-notification-alert";
import Web3 from 'web3';
import abi from '../abi1'

const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
  height: 64px;
  width:64px;
`;


async function check_metamask(){
  try{
      if (window.ethereum) {
          await window.ethereum.request({ method: 'eth_requestAccounts' });
          window.web3 = await new Web3(window.ethereum);
          return true;
      }  
      return false;
  }catch(e){
      console.log(e)
      throw("")

  }
}


function Map() {
  const [mintPrice , setMintPrice] = useState("0")
  const [url , setUrl] = useState("")
  const [fileName , setFileName] = useState("")
  const [file,setFile] = useState(null)
  const [eventAddress , setEventAddress] = useState(0)
  const [ignored, forceUpdate] = useReducer(x => x + 1, 0);
  const [loading, setLoading] = useState(false);
  const [urlBar, setUrlBar] = useState("");
  const [color, setColor] = useState("#ffffff"); 
  const notificationAlertRef = React.useRef(null);

  const notify = (messagex) => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: "tr",
      message: (
        <div>
          <div>
              {messagex}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    notificationAlertRef.current.notificationAlert(options);
  };
  return (
    <>
      <div className="content">
      <div className="react-notification-alert-container">
          <NotificationAlert ref={notificationAlertRef} />
        </div>
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Mint Video Nft</h5>
              </CardHeader>
              <CardBody>
              <div style={{'flex':1}}> 

                <div style={{'display':'flex'}} onClick={()=>{
                    let input = document.createElement('input');
                    input.type = 'file';
                    input.setAttribute('accept',"video/mp4,video/x-m4v,video/*")
                    input.onchange = _ => {
                      let files =   Array.from(input.files);
                      if (files && files[0]) {

                        var FR= new FileReader();

                        FR.addEventListener("load", function(e) {
                          setFileName(files[0].name)
                          setFile(files[0])
                          forceUpdate()
                          //setVideoData(e.target.result)
                        }); 

                        FR.readAsDataURL( files[0] );
                    };

                    }
                    input.click();

                }}>
                  
                  <img height={64} width={64} src={pic}></img> 
                  <span style={{'marginLeft':'10px',alignSelf:'center'}}>{fileName}</span>
                </div>
                <Label for="mint_price">Mint Price (TFUEL)</Label>
                <Input
                  name="mint_price"
                  id="mint_price"
                  placeholder="Enter Mint Price" 
                  style={{'width':'90%'}}
                  onChange={(e)=>{
                    setMintPrice(e.target.value)
                  }}
                />
                <Label for="video_event_address">Event NFT Id</Label>
                <Input
                  name="video_event_address"
                  id="video_event_address"
                  type='number'
                  placeholder="Enter Video Event Address" 
                  style={{'width':'90%'}}
                  onChange={(e)=>{
                    setEventAddress(Number(e.target.value))
                  }}
                />
         
                
              </div>
                         
              {!loading && (<Button color="primary" style={{'marginTop':'20px'}} onClick={async ()=>{
                      setLoading(true)

                      let rawResponse = await fetch('https://api.thetavideoapi.com/upload', {
                        method: 'POST',
                        headers: {
                          'x-tva-sa-id': 'srvacc_ixq54xge28jns3n59bmsaqnfe',
                          'x-tva-sa-secret': '3484sb3d0i6veeqwwv680w4yc4kuv30k'
                        },
                      });
                      let content = await rawResponse.json();
                      let presigned = content['body']['uploads'][0]['presigned_url']; 
                      console.log(presigned)

                      await fetch(presigned, {
                        method: 'put',
                        headers: {
                          'Content-Type': 'application/octet-stream'
                        },
                        body: file
                      });

                      let rawResponsex = await fetch('https://api.thetavideoapi.com/video', {
                        method: 'POST',
                        headers: {
                          'x-tva-sa-id': 'srvacc_ixq54xge28jns3n59bmsaqnfe',
                          'x-tva-sa-secret': '3484sb3d0i6veeqwwv680w4yc4kuv30k',
                          'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({ "source_upload_id": content['body']['uploads'][0]['id'],  "playback_policy": "public"})
                      });
                      let contentx = await rawResponsex.json();
                      let vid = contentx['body']['videos'][0]['id']
                      let xxs = async ()=>{
                        let rexs = await fetch(`https://api.thetavideoapi.com/video/${vid}`, {
                          method: 'GET',
                          headers: {
                            'x-tva-sa-id': 'srvacc_ixq54xge28jns3n59bmsaqnfe',
                            'x-tva-sa-secret': '3484sb3d0i6veeqwwv680w4yc4kuv30k',
                          }
                        });
                        rexs = await rexs.json()
                        return rexs;
                      }
                      let vx = setInterval(async ()=>{
                        let x = await xxs()
                        if(!x['body']['videos'][0]['playback_uri']){
                          let prg =x['body']['videos'][0]['progress']
                          setUrlBar(prg)
                        }else{
                          clearInterval(vx)
                          setUrlBar('Minting on theta network...')
                          let isMetmask = await check_metamask()
                          if(!isMetmask){
                              notify('Please install metamask!')
                              setLoading(false)
                              return
                          }
                          let chani_id =await window.web3.eth.net.getId()
                          if(chani_id!=365){
                              notify("Please Switch To theta tesnet!")
                              setLoading(false)
                              return
                          }
                          const address = (await window.web3.eth.getAccounts())[0]
                          const myContract = new window.web3.eth.Contract(
                            abi,
                           "0x07D895F3f3f8861a72C80e7a71fae8DfE547D875"
                          );
                          await myContract.methods.mint(x['body']['videos'][0]['playback_uri'], eventAddress ,Web3.utils.toWei(mintPrice, 'ether')).send({ from: address });
                          setUrlBar(' ')
                          setUrl(x['body']['videos'][0]['playback_uri'])
                          setLoading(false)
                          
                        }
                      },500)
                      
 
                      
                  }}>Mint</Button>)}
                  <ClipLoader color={color} loading={loading} css={override} size={150} />
                  <div style={{'width':'100%'}}>
                    {urlBar!="" && (
                      <div style={{'display':'flex',flexDirection:'column', width:'100%'}}>
                        <span style={{'alignSelf':'center'}}>
                          {urlBar}
                        </span>
                        <a href={url}>{url}</a>
                      </div>
                    )}   
                  </div>      
                 
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default Map;
