function makeid(length) {
  var result           = '';
  var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  var charactersLength = characters.length;
  for ( var i = 0; i < length; i++ ) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
 }
 return result;
}
const override = css`
  display: block;
  margin: 0 auto;
  border-color: red;
  height: 64px;
  width:64px;
`;

function Dashboard(props) {
  const [eventData , setEventData] = useState({})
  const [loading, setLoading] = useState(false);
  const [urlBar, setUrlBar] = useState("");
  const [color, setColor] = useState("#ffffff"); 
  const [eventName , setEventName] = useState("")
  const [eventKey , setEventKey] = useState("")
  const [teamNumbers , setTeamNumbers] = useState(0)
  const [ignored, forceUpdate] = useReducer(x => x + 1, 0);
  const notificationAlertRef = React.useRef(null);

  const notify = (messagex) => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: "tr",
      message: (
        <div>
          <div>
              {messagex}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    notificationAlertRef.current.notificationAlert(options);
  };

  return (
    <>
      <div className="content">
        <div className="react-notification-alert-container">
          <NotificationAlert ref={notificationAlertRef} />
        </div>
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Host Your Game Event / Host Your Daily Stream</h5>
              </CardHeader>
              <CardBody>
                  <Label for="event_name">Event Name</Label>
                  <Input
                    name="event_name"
                    id="event_name"
                    placeholder="Enter Event Name" 
                    style={{'width':'30%'}}
                    onChange={(e)=>{
                      setEventName(e.target.value)
                    }}
                  />
                  <Label for="event_stream_key">Theta Stream Key (Center Display)</Label>
                  <Input
                    name="event_stream_key"
                    id="event_stream_key"
                    placeholder="Enter Theta Stream Key (Center Display)" 
                    style={{'width':'30%'}}
                    onChange={(e)=>{
                      setEventKey(e.target.value)
                    }}
                  />
                  <Label for="team_numbers">Number Of Teams</Label>
                  <Input
                    type="number"
                    name="team_numbers"
                    id="team_numbers"
                    placeholder="Enter Number Of Teams"
                    style={{'width':'30%'}}
                    onChange={(e)=>{
                      setTeamNumbers(Number(e.target.value))
                    }}
                  />
                  {Array.from(Array(teamNumbers), (e, i) => {
                    let x = eventData
                    if(!x.hasOwnProperty(i)){
                      x[i]={'team_name':'','p1':{},'p2':{},'p3':{},'p4':{}}
                      setEventData(x)
                    }
                    
                    return (
                      
                      <div key={i} style={{'marginTop':'20px'}}>
                          <hr
                                    style={{
                                        color: '#1d8cf8',
                                        backgroundColor: '#1d8cf8',
                                        height: 1
                                    }}
                                />
                          <Label for="team_name">Team Name</Label>
                          <Input
                            name="team_name"
                            id="team_name"
                            placeholder="Enter Team Name" 
                            style={{'width':'30%'}}
                            onChange={(e)=>{
                              let x = eventData
                              x[i]['team_name'] = e.target.value
                              setEventData(x)
                            }}
                          />
                          <div style={{'display':'flex','width':'100%','marginTop':'10px'}}>
                          <div style={{'flex':1}}> 
                            <div>
                              <Label >Player 1</Label>
                            </div>
                            <div onClick={()=>{
                                let input = document.createElement('input');
                                input.type = 'file';
                                input.onchange = _ => {
                                  let files =   Array.from(input.files);
                                  if (files && files[0]) {
                                    x[i]['p1']['imgx'] = files[0]

                                  var FR= new FileReader();

                                  FR.addEventListener("load", function(e) {
                                    let x =eventData
                                    x[i]['p1']['img']= e.target.result
                                    forceUpdate()
                                  }); 

                                  FR.readAsDataURL( files[0] );
                                };

                                }
                                input.click();

                            }}>
                             
                              <img height={64} width={64} src={eventData[i]['p1'].hasOwnProperty('img')?eventData[i]['p1']['img'] :pic}></img>
                            </div>
                            <Label for="player_name">Name</Label>
                            <Input
                              name="player_name"
                              id="player_name"
                              placeholder="Enter Name" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p1']['name'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            <Label for="stream_key">Theta Stream Key</Label>
                            <Input
                              name="stream_key"
                              id="stream_key"
                              placeholder="Enter Theta Stream Key" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p1']['key'] = e.target.value
                                setEventData(x)
                              }}
                            />
                               
                          </div>
                          <div style={{'flex':1}}> 
                            <div>
                              <Label >Player 2</Label>
                            </div>
                            <div onClick={()=>{
                                let input = document.createElement('input');
                                input.type = 'file';
                                input.onchange = _ => {
                                  let files =   Array.from(input.files);
                                  if (files && files[0]) {
                                    x[i]['p2']['imgx'] = files[0]

                                  var FR= new FileReader();

                                  FR.addEventListener("load", function(e) {
                                    let x =eventData
                                    x[i]['p2']['img']= e.target.result
                                    forceUpdate()
                                  }); 

                                  FR.readAsDataURL( files[0] );
                                };

                                }
                                input.click();

                            }}>
                             
                              <img height={64} width={64} src={eventData[i]['p2'].hasOwnProperty('img')?eventData[i]['p2']['img'] :pic}></img>
                            </div>
                            <Label for="player_name">Name</Label>
                            <Input
                              name="player_name"
                              id="player_name"
                              placeholder="Enter Name" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p2']['name'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            <Label for="stream_key">Theta Stream Key</Label>
                            <Input
                              name="stream_key"
                              id="stream_key"
                              placeholder="Enter Theta Stream Key" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p2']['key'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            
                          </div>
                          <div style={{'flex':1}}> 
                            <div>
                              <Label >Player 3</Label>
                            </div>
                            <div onClick={()=>{
                                let input = document.createElement('input');
                                input.type = 'file';
                                input.onchange = _ => {
                                  let files =   Array.from(input.files);
                                  if (files && files[0]) {
                                    x[i]['p3']['imgx'] = files[0]
                                  var FR= new FileReader();

                                  FR.addEventListener("load", function(e) {
                                    let x =eventData
                                    x[i]['p3']['img']= e.target.result
                                    forceUpdate()
                                  }); 

                                  FR.readAsDataURL( files[0] );
                                };

                                }
                                input.click();

                            }}>
                             
                              <img height={64} width={64} src={eventData[i]['p3'].hasOwnProperty('img')?eventData[i]['p3']['img'] :pic}></img>
                            </div>
                            <Label for="player_name">Name</Label>
                            <Input
                              name="player_name"
                              id="player_name"
                              placeholder="Enter Name" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p3']['name'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            <Label for="stream_key">Theta Stream Key</Label>
                            <Input
                              name="stream_key"
                              id="stream_key"
                              placeholder="Enter Theta Stream Key" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p3']['key'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            
                          </div>
                          <div style={{'flex':1}}> 
                            <div>
                              <Label >Player 4</Label>
                            </div>
                            <div onClick={()=>{
                                let input = document.createElement('input');
                                input.type = 'file';
                                input.onchange = _ => {
                                  let files =   Array.from(input.files);
                                  if (files && files[0]) {
                                    x[i]['p4']['imgx'] = files[0]

                                  var FR= new FileReader();

                                  FR.addEventListener("load", function(e) {
                                    let x =eventData
                                    x[i]['p4']['img']= e.target.result
                                    forceUpdate()
                                  }); 

                                  FR.readAsDataURL( files[0] );
                                };

                                }
                                input.click();

                            }}>
                             
                              <img height={64} width={64} src={eventData[i]['p4'].hasOwnProperty('img')?eventData[i]['p4']['img'] :pic}></img>
                            </div>
                            <Label for="player_name">Name</Label>
                            <Input
                              name="player_name"
                              id="player_name"
                              placeholder="Enter Name" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p4']['name'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            <Label for="stream_key">Theta Stream Key</Label>
                            <Input
                              name="stream_key"
                              id="stream_key"
                              placeholder="Enter Theta Stream Key" 
                              style={{'width':'90%'}}
                              onChange={(e)=>{
                                let x = eventData
                                x[i]['p4']['key'] = e.target.value
                                setEventData(x)
                              }}
                            />
                            
                          </div>
                          </div>
                      </div>
                    )
                  })}
                  {!loading && (<Button color="primary" style={{'marginTop':'20px'}} onClick={async ()=>{
                      setLoading(true)
                      let eid = makeid(8)
                      let jk = eventData
                      for(let x in jk){
                        for(let i=1;i<=4;i++){
                          if(jk[x][`p${i}`].hasOwnProperty('img') && jk[x][`p${i}`]['img']){
                            let r = await uploadtonftstorage(jk[x][`p${i}`]['imgx'])
                            if(r){
                              jk[x][`p${i}`]['img'] = 'https://'+JSON.parse(r)['value']['cid']+'.ipfs.infura-ipfs.io/'
                            }else{
                              jk[x][`p${i}`]['img'] ="/player.png"
                            }
                            delete jk[x][`p${i}`]['imgx']
                          }
                        }
                      }
                      console.log(jk)
                      let eventUrl = `https://streamverse.b-cdn.net/?meeting_id=${eid}_0&t=${btoa(eventName)}`
                      jk['event_url'] = eventUrl;
                      jk['key'] = eventKey;
                      let r =JSON.parse(await uploadtoweb3(jk))
                      console.log( 'https://'+r.cid+'.ipfs.infura-ipfs.io/')
                      let isMetmask = await check_metamask()
                      if(!isMetmask){
                          notify('Please install metamask!')
                          setLoading(false)
                          return
                      }
                      let chani_id =await window.web3.eth.net.getId()
                      if(chani_id!=365){
                          notify("Please Switch To theta tesnet!")
                          setLoading(false)
                          return
                      }
                      const address = (await window.web3.eth.getAccounts())[0]
                      const myContract = new window.web3.eth.Contract(
                        abi,
                       "0x219bb8132b6Cb48D6aB9977843EBe608b1bc7C2B"
                      );
                      await myContract.methods.mint(r.cid, eid).send({ from: address });
                      setLoading(false)
                      setUrlBar(eventUrl)
                  }}>Host</Button>)}
                  <ClipLoader color={color} loading={loading} css={override} size={150} />
                  <div>
                    {urlBar!="" && (
                      <a href={urlBar}>{urlBar}</a>
                    )}   
                  </div>          
                 </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default Dashboard;
