import React, { useState } from "react";

// reactstrap components
import { Card, CardHeader, CardBody, Row, Col } from "reactstrap";
import {
  request,
  gql
} from 'graphql-request'
import Web3 from 'web3';

async function check_metamask(){
  try{
      if (window.ethereum) {
          await window.ethereum.request({ method: 'eth_requestAccounts' });
          window.web3 = await new Web3(window.ethereum);
          return true;
      }  
      return false;
  }catch(e){
      console.log(e)
      throw("")

  }
}

function Icons() {
  
  const [events, setEvents] = useState([])
  const notificationAlertRef = React.useRef(null);

  const notify = (messagex) => {
    var color = Math.floor(Math.random() * 5 + 1);
    var type;
    switch (color) {
      case 1:
        type = "primary";
        break;
      case 2:
        type = "success";
        break;
      case 3:
        type = "danger";
        break;
      case 4:
        type = "warning";
        break;
      case 5:
        type = "info";
        break;
      default:
        break;
    }
    var options = {};
    options = {
      place: "tr",
      message: (
        <div>
          <div>
              {messagex}
          </div>
        </div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: 7,
    };
    notificationAlertRef.current.notificationAlert(options);
  };

  (async ()=>{
    try{
      let esx = []
      let isMetmask = await check_metamask()
      if(!isMetmask){
          notify('Please install metamask!')
          return
      }
      let chani_id =await window.web3.eth.net.getId()
      if(chani_id!=365){
          notify("Please Switch To theta tesnet!")
          return
      }
      const address = (await window.web3.eth.getAccounts())[0]
      const query = gql `{
        events(where: { owner: ${JSON.stringify(address.toLowerCase())} }){
        id
          url
         idString
      
        }
      }
    `
    let data = await request('https://thetagraph.overclockedbrains.co/subgraphs/name/Testo', query)
    for(let e of data['events']){
      let u = 'https://'+e['url']+'.ipfs.infura-ipfs.io/'
      let resx = await fetch(u)
      resx = await resx.json()
      u = resx['event_url']
      esx.push(u)
    }
    setEvents(esx)
    }catch(e){}
  })()
  return (
    <>
      <div className="content">
        <Row>
          <Col md="12">
            <Card>
              <CardHeader>
                <h5 className="title">Your Events</h5>
              </CardHeader>
              <CardBody>
              { events.map(function(d, idx){
                  return (<div style={{'marginTop':'15px'}} key={idx}><a href={d}>{d}</a> </div>)
                })}
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
}

export default Icons;
